@use 'base/variables' as v;
@use 'base/mixins' as m;

h1, h2, h3 {
    margin: 0;
    line-height: 1.2;
}
h1 {
    font-size: 3.8rem;
    @include m.tablet { 
        font-size: 4.2rem;
    }
    font-family: v.$fuenteSecundaria;
}
h2 {
    font-size: 3rem;
    @include m.tablet { 
        font-size: 3.6rem;
    }
    font-family: v.$fuenteSecundaria;
}
h3 {
    font-size: 2.4rem;
    font-family: v.$fuenteText;
    @include m.tablet { 
        font-size: 3rem;
        
    }
}
[class$="heading"] {
    margin: 2rem 0;
}
a {
    text-decoration: none;
    color: v.$negro;
}

p {
    color: v.$negro;
    font-size: 1.6rem;
    line-height: 1.8;
    font-family: v.$fuenteText;
}

