@use 'variables' as v; 
@use 'mixins' as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}

body {
    font-family: v.$fuentePrincipal;
    font-size: 1.6rem;
    // background-color: darken(v.$grisClaro, .5%);
}
.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
    @include m.desktopXXL { 
        max-width: 150rem;
    }
}
img {
    max-width: 100%;
    display: block;
}
ul {
    padding:0;
    list-style: none;
    margin: 0;
}
.mt-5 {
    margin-top: 5rem;
}
